.video {
  box-sizing: border-box;
  padding: 17px 20px 0px 20px;
}

@media (min-width: @tablet-width) {
  .video {
    width: 768px;
    box-sizing: border-box;
    padding: 58px 60px 40px 60px;
  }
}

@media (min-width: @desktop-width) {
  .video {
    width: 1200px;
    box-sizing: border-box;
    padding: 62px 200px 120px 200px;
  }
}

.video__img {
  margin-top: 38px;
}

.video__title {
  .text-block__title();
}

.video__description {
  .text-block__description();
}
